@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap");
* {
  font-family: "Montserrat";
}

@import-normalize;
.header {
  margin-top: 26px;
  margin-left: 63px;
  width: 1785px;
  height: 97px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__wrapper-logo-nav {
  width: 867px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header nav {
  width: 715px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header nav span {
  display: block;
  margin-top: 7px;
  width: 22px;
  height: 2px;
  background-color: #4C858B;
  border-radius: 5px;
}
.header nav span:nth-child(1) {
  margin-top: 0;
}
.header__nav {
  padding: 0;
  margin-left: 31px;
  width: 665px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav__item {
  font-size: 16px;
  font-weight: 700;
  line-height: 23px;
  text-transform: uppercase;
  list-style-type: none;
}
.header__nav__item a {
  text-decoration: none;
  color: #4C858B;
}
.header__circle {
  width: 56px;
  height: 56px;
  border-radius: 100%;
  border: 1px solid #4C858B;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header__phone {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #4C858B;
}
.header__phone span {
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  color: #000;
}
.header__callMeBack {
  font-size: 14px;
  font-weight: 700;
  line-height: 17px;
  color: #AE2236;
  text-transform: uppercase;
}
.header__divider {
  width: 150px;
  border: 1px dashed #AE2236;
}
.header__control-wrapper {
  width: 204px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__circle-control {
  width: 56px;
  height: 56px;
  border-radius: 100%;
  border: 1px solid #E5E5E5;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social__wrapper {
  width: 120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}